<template>
  <div>
  <h2>Search operators</h2>

  <p>The search field utilizes mongodb's <b>$search</b> operator. As result, the query syntax is the same. You can learn more about in mongodb's documentation:
  <a href="https://docs.mongodb.com/manual/reference/operator/query/text/#search-field" target="_blank">https://docs.mongodb.com/manual/reference/operator/query/text/#search-field</a></p>

  <h3>Search protocol prefixes</h3>
  <p>Put <code><strong>prefix:</strong></code> in front of a prefix code. For example, <code><strong>protocol:6d02 cats</strong></code><a href="http://localhost:8080/#/result/cHJlZml4OjZkMDIgY2F0cw==/0">[see on Oyo]</a></p>

  <h3>Search hashtags</h3>
  <p>Put <code><strong>#</strong></code> in front of a word and enclose the word in double quotes (<code><strong>"</strong></code>). For example: <code><strong>"#bitquery"</strong></code>
  <a href="https://oyo.cash/#/result/IiNiaXRxdWVyeSI=/0">[see on Oyo]</a></p>

  <h3>Exclude words from your search</h3>
  <p>Put <code><strong>-</strong></code> in front of a word you want to leave out. For example, <code><strong>bitcoin -core</strong></code>
  <a href="https://oyo.cash/#/result/Yml0Y29pbiAtY29yZQ==/0">[see on Oyo]</a></p>

  <h3>Exact match</h3>
  <p>Put a word or phrase inside quotes. For example, <code><strong>"bitcoin unlimited"</strong></code>.
  <a href="https://oyo.cash/#/result/ImJpdGNvaW4gdW5saW1pdGVkIg==/0">[see on Oyo]</a></p>

  <h3>Combining searches</h3>
  <p>
    Put a word or phrase inside quotes for mandatory match (AND). Use a word without quotes for optional match (OR). Examples:<br/>
    <code><strong>"moneybutton" "wordpress"</strong></code> (searches for "moneybutton AND wordpress") <a href="https://oyo.cash/#/result/Im1vbmV5YnV0dG9uIiAid29yZHByZXNzIg==/0">[see on Oyo]</a><br/>
    <code><strong>liquid lightning</strong></code> (searches for "liquid OR lightning") <a href="https://oyo.cash/#/result/bGlxdWlkIGxpZ2h0bmluZw==/0">[see on Oyo]</a><br/>
    <code><strong>"market" bull bear</strong></code> (searches for "market AND (market OR bull OR bear)") <a href="https://oyo.cash/#/result/Im1hcmtldCIgYnVsbCBiZWFy/0">[see on Oyo]</a>
  </p>
  </div>
</template>

<script>
  export default {
    name: 'HowToSearchOnOyo',
    components: {
    }
  };
</script>

<style>
</style>



