<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'App',
  }
</script>

<style>
html, body {
    width: 100%;
    	margin: 0;
        padding: 0;
        border: none;
        height: 100%;
        background: transparent;
        font-size: 13px;
        display: block;
        font-family: arial, sans-serif;
}
body{
	margin: 0;
}
.grid {
  min-height: 100%;
	display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: auto 1fr auto;
}
.header{
  grid-column: span 12;
	display:relative;
	padding-top: 18px;
	padding-left: 13px;
	padding-right: 35px;
	background-color: #FAFAFA;
	height: 70px;
	border-bottom: 0.5px solid #EBEBEB;
	display:flex;
	justify-content: space-between;
}
.search{
  align-items: center;
  display:flex;
}
.left{
  position:relative;
}
#g_logo{
  margin-right:18px;
  height: 44px;
  width: 120px;
}
.s_results{
  grid-column: span 10;
  font-family: arial, sans-serif;
  margin: 15px 165px 51px;
}
.form {
  margin-bottom: 45px;
}
.form .field-block {
  border-bottom: 1px solid #eee;
  color: #222;
  min-width: 400px;
  padding-top: 18px;
  padding-bottom: 10px;
  padding-right: 20px;
  position: relative;
}
.form h4 {
  display: block;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-size: 100%;
  font-weight: bold;
  font-family: arial,sans-serif;
  color: #222;
}
.form input {
  width: 280px;
}
.form .form-buttons {
  display: block;
  font-family: arial,sans-serif;
  padding-top: 18px;
  text-align: right;
}
.form .form-buttons input[type="submit"] {
    background-color: #f2f2f2;
    color: #757575;
    font-weight: bold;
    height: 36px;
    width: 142px;
    border: 1px;
    border-radius: 2px;
    margin: 11px 4px;
    text-align: center;
}
.form .form-buttons input[type="submit"]:hover {
    border: 1px solid #c6c6c6;
}
.form .form-buttons input[type="submit"]:focus {
    border: 1px solid #4d90fe;
}
@media screen and (max-width: 1024px) {
  .s_results{
    grid-column: span 12;
    font-family: arial, sans-serif;
    margin: 15px 165px 51px;
  }
}
@media screen and (max-width: 768px) {
  .s_results{
    grid-column: span 12;
    font-family: arial, sans-serif;
    margin: 15px 10px 51px;
  }
}
</style>



