<template>
    <div class="header">
      <div class="left">
        <div class="search">
          <a href="/">
            <img id="g_logo" src="../assets/images/logo-small.png" srcset="../assets/images/logo-small@2x.png 2x, ../assets/images/logo-small@3x.png 3x" name="g_logo" />
          </a>
          <h1>{{ title }}</h1>
        </div>
      </div>
      <!--
      <div class="g_apps right">
        <img id="profile_pic" src="images/profile.png" name="profile_pic" />
      </div>
      //-->
    </div>
</template>

<script>
  export default {
      name: 'HeaderWithTitle',
      props: {
        title: String
      }
  };
</script>

<style>
h1{
  color: #5f6368;
  display: block;
  font-family: 'Google Sans',sans-serif;
  font-size: 20px;
  font-weight: 400;
  margin: 0;
  padding: 13px 8px 8px 5px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



