<template>
  <div class="grid">
    <div class="header">
      <div class="left">
        <div class="search">
          <a href="/">
            <img id="g_logo" src="../../assets/images/logo-small.png" srcset="../../assets/images/logo-small@2x.png 2x, ../../assets/images/logo-small@3x.png 3x" name="g_logo" />
          </a>
          <form @submit.prevent="changeSearchRoute(1)">
            <input type="text" value="searchTextInput" name="searchTextInput" v-model="searchTextInput" v-on:input="searchTextInput = $event.target.value" />
            <button class="loupe"><img  src="../../assets/images/search.png" srcset="../../assets/images/search@2x.png 2x, ../../assets/images/search@3x.png 3x" /></button>
          </form>
        </div>
      </div>
      <!--
      <div class="g_apps right">
        <img id="profile_pic" src="images/profile.png" name="profile_pic" />
      </div>
      //-->
    </div>

    <div class="s_results" v-if="loading === false">
      <div class="n_results">
        About {{ matchesCount }} results
      </div>
      <div class="s_result" v-for="(item, index) in resultAllItems">
        {{ findText(item, index) }}
        <a class="title" :href= "'https://explorer.bitcoin.com/bch/tx/' + item.tx.h">
        {{ resultLongestString[index] | toTitle() }}</a>
        <div class="content">
          <p class="s_url">
            <a class="s_url" :href= "'https://explorer.bitcoin.com/bch/tx/' + item.tx.h">{{ item.tx.h }}</a>
          </p>
          <div>
            <p class="s_detail">
              <span class="date" v-if="item.blk">{{ unixTimeToDatetime(item.blk.t) }} - </span><span class="date" v-else>Unconfirmed - </span><text-highlight :queries="highlightArray" :highlightStyle="'background: #FFFFFF;'" :highlightClass="'text_highlight'">{{ resultLongestString[index] }}</text-highlight>
            </p>
          </div>
        </div>
      </div>

      <!--
      <h3>
        Searches related to build this webpage
      </h3>
      <div class="r_searches">
        <ul id="l_left">
          <li>
            <a href="#" class="r_item">test 1</a>
          </li>
          <li>
            <a href="#" class="r_item">test 1</a>
          </li>
          <li>
            <a href="#" class="r_item">test 1</a>
          </li>
        </ul>
        <ul id="l_right">
          <li>
            <a href="#" class="r_item">test 1</a>
          </li>
          <li>
            <a href="#" class="r_item">test 1</a>
          </li>
        </ul>
      </div>
      //-->
      <div class="page_number">

        <paginate
          v-model="page"
          :pageCount="parseInt((matchesCount + itemsPerPage)/itemsPerPage)"
          :pageRange="9"
          :clickHandler="changeSearchRoute"
          :prevText="'Previous'"
          :nextText="'Next'"
          :container-class="'pagination'"
          :page-class="'page-item'"
          :prev-class="'prev-item'"
          :next-class="'next-item'"
          :active-class="'active-item'"
          :hide-prev-next="true">
        </paginate>
      </div>
    </div>
    <div class="s_results" v-else>
      <div class="n_results">
        Loading...
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Footer from '@/components/Footer'

export default {
  name: 'Result',
  props: {
    search: {type: String, required: true},
    skip: {type: String, required: true}
  },
  data () {
    return {
      highlightArray: [],
      orSearchArray: [],
      andSearchArray: [],
      offset: null,
      page: 1,
      itemsPerPage: null,
      matchesCount: null,
      searchText: null,
      searchTextInput: null,
      resultData: null,
      resultConfirmedItems: [],
      resultUnconfirmedItems: [],
      resultAllItems: [],
      resultLongestString: [],
      resultConcatinatedStrings: [],
      loading: true,
      errored: false
    }
  },
  components: {
    Footer
  },
  mounted () {
    this.offset = parseInt(this.skip)
    this.itemsPerPage = parseInt(localStorage.getItem('resultsPerPage'))
    this.page = parseInt(this.offset/this.itemsPerPage) + 1
    this.searchText = this.searchTextInput = Base64.decode(this.search)
    this.computeSearchArray()
    this.searchTheBlockchainForText()
    this.getMatchCount()
  },
  methods: {
    unixTimeToDatetime (unix_timestamp) {
      let a = new Date(parseInt(unix_timestamp) * 1000)
      let months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
      let year = a.getFullYear()
      let month = months[a.getMonth()]
      let date = a.getDate()
      let hour = ('0' + a.getHours()).substr(-2)
      let min = ('0' + a.getMinutes()).substr(-2)
      let time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min
      return time
    },
    computeSearchArray () {
      let tmpSearchText = this.searchText

      if (tmpSearchText.startsWith("prefix:")) {
        tmpSearchText = tmpSearchText.replace(/^prefix:\s*|\s*$/g, '')
        tmpSearchText = tmpSearchText.replace(/^\S+\s*/gu, '')
      }

      this.andSearchArray = []
      let pattern = /"(.*?)"/gu;
      let current;
      while(current = pattern.exec(tmpSearchText)) {
        this.andSearchArray.push(current[1].toLowerCase())
      }
      pattern = /'(.*?)'/gu;
      while(current = pattern.exec(tmpSearchText)) {
        this.andSearchArray.push(current[1].toLowerCase())
      }

      this.orSearchArray = []
      tmpSearchText = tmpSearchText.replace(/["',!?]/giu, '')
      this.orSearchArray = tmpSearchText.toLowerCase().split(' ')
      let orSearchArrayTmp = this.orSearchArray
      for (let orSIndex = 0; orSIndex < orSearchArrayTmp.length; ++orSIndex) {
        if (orSearchArrayTmp[orSIndex].endsWith('s') || orSearchArrayTmp[orSIndex].endsWith('ing')) {
          let rootString = orSearchArrayTmp[orSIndex].replace(/ings|ing|s$/gi, '')
          if(this.orSearchArray.indexOf(rootString) === -1) {
            this.orSearchArray.push(rootString)
          }
          if(this.orSearchArray.indexOf(rootString + 's') === -1) {
            this.orSearchArray.push(rootString + 's')
          }
          if(this.orSearchArray.indexOf(rootString + 'ing') === -1) {
            this.orSearchArray.push(rootString + 'ing')
          }
          if(this.orSearchArray.indexOf(rootString + 'ings') === -1) {
            this.orSearchArray.push(rootString + 'ings')
          }
        }
      }

      this.highlightArray = this.andSearchArray.concat(this.orSearchArray)
    },
    findText (item, itemIndex) {
      let resultString = ''

      for (let index = 0; index < item.out.length; ++index) {
        for (let sIndex = 0; sIndex < 16; ++sIndex) {
          let varName = 'item.out[index].s' + sIndex
          if (typeof eval(varName) !== 'undefined') {
            if (this.orSearchArray.some(oItem => eval(varName).toLowerCase().includes(oItem))) {
              if (this.andSearchArray.every(aItem => eval(varName).toLowerCase().includes(aItem))) {
                if (resultString.length <= eval(varName).length) {
                  resultString = eval(varName)
                }
              }
            }
          }
        }
      }

      if (resultString === '') {
        for (let index2 = 0; index2 < item.out.length; ++index2) {
          for (let sIndex2 = 0; sIndex2 < 16; ++sIndex2) {
            let varName = 'item.out[index2].s' + sIndex2
            if (typeof eval(varName) !== 'undefined') {
              if (this.andSearchArray.some(aItem2 => eval(varName).toLowerCase().includes(aItem2))) {
                if (resultString.length < eval(varName).length) {
                  resultString = eval(varName)
                }
              }
            }
          }
        }
      }

      this.resultLongestString[itemIndex] = resultString
    },
    changeSearchRoute (page) {
      if (this.searchTextInput) {
        this.searchText = this.searchTextInput
        this.offset = parseInt((page - 1) * this.itemsPerPage)
        this.page = parseInt(this.offset/this.itemsPerPage) + 1
        this.$router.push('/result/' + Base64.encode(this.searchText) + '/' + this.offset)
        this.computeSearchArray()
        this.searchTheBlockchainForText()
        this.getMatchCount()
      }
    },
    getMatchCount () {
      let matchPhrase = this.searchText
      let queryMatch = {}
      queryMatch['$and'] = []
      if (matchPhrase.startsWith("prefix:")) {
        matchPhrase = matchPhrase.replace(/^prefix:\s*|\s*$/g, '')
        var prefixArray = matchPhrase.split(" ")
        queryMatch['$and'].push({'out.h1': prefixArray[0]})
        matchPhrase = matchPhrase.replace(/^\S+\s*/gu, '')
      }
      if (matchPhrase) {
        queryMatch['$and'].push({'$text': {'$search': '' + matchPhrase + ''}})
      }
      let query = {
        'v': 3,
        'q': {
          'aggregate': [
            {'$match': queryMatch},
            {'$count': "count"}
          ]
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = localStorage.getItem('bitdbNode') + b64

      // Attach API KEY as header
      let header = {
        headers: { key: localStorage.getItem('bitdbApiKey') }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data.c.length > 0) {
            this.matchesCount = response.data.c[0].count
          } else {
            this.matchesCount = 0
          }
        })
        .catch(error => {
          console.log(error)
          this.errored = true
        })
    },
    searchTheBlockchainForText () {
      this.loading = true
      this.errored = false
      // nullify cached results
      this.resultLongestString = []
      this.resultConcatinatedStrings = []

      let matchPhrase = this.searchText
      let queryMatch = {}
      queryMatch['$and'] = []
      if (matchPhrase.startsWith("prefix:")) {
        matchPhrase = matchPhrase.replace(/^prefix:\s*|\s*$/g, '')
        var prefixArray = matchPhrase.split(" ")
        queryMatch['$and'].push({'out.h1': prefixArray[0]})
        matchPhrase = matchPhrase.replace(/^\S+\s*/gu, '')
      }
      if (matchPhrase) {
        queryMatch['$and'].push({'$text': {'$search': '' + matchPhrase + ''}})
      }

      let query = {
        'v': 3,
        'q': {
          'find': queryMatch,
          'skip': parseInt(this.offset),
          'limit': this.itemsPerPage
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = localStorage.getItem('bitdbNode') + b64

      let header = {
        headers: { key: localStorage.getItem('bitdbApiKey') }
      }

      axios
        .get(url, header)
        .then(response => {
          this.resultData = response.data
          this.resultUnconfirmedItems = this.resultData.u
          this.resultConfirmedItems = this.resultData.c
          this.resultAllItems = this.resultUnconfirmedItems.concat(this.resultConfirmedItems);
          this.loading = false
        })
        .catch(error => {
          console.log(error)
          this.errored = true
            this.loading = false
        })
    }
  }
}
</script>

<style>
.search input[type="text"]{
  box-sizing:border-box;
  height:46px;
  width: 630px;
  font-family: sans-serif;
  font-size: 16px;
  padding: 0 20px;
  border: 0.5px solid #d9d9d9;
  box-shadow: 0 4px 2px -2px #e6e6e6;
}
.search input[type="text"]:hover{
  box-shadow: 0px 2px 8px #dddddd;
  outline: none;
}
.loupe{
  background-color: Transparent;
  background-repeat:no-repeat;
  border: none;
  cursor:pointer;
  overflow: hidden;
  outline:none;
  position: absolute;
  top:10px;
  right:20px;
}
.header a:link, .header a:visited{
  color: grey;
  text-decoration: none;
}
.header a:hover{
  color:black;
}
#all{
  color: #4285F4;
  font-weight: bold;
}
#profile_pic{
  width: 32px;
  height: 32px;
  border-radius: 100%;
}
.g_apps img{
  margin-left: 18px;
}
.s_results{
  grid-column: span 10;
  font-family: arial, sans-serif;
  margin: 15px 165px 51px;
}
.n_results{
  font-size: 13px;
  color: grey;
  margin-bottom: 15px;
}
.title{
  font-size: 18px;
  text-decoration: none;
  color: #1a0dab;
}
.title:visited{
  color: #609;
}
.title:hover{
  text-decoration: underline;
}
.s_url{
  margin:0;
  color: green;
  font-size: 14px;
  text-decoration: none;
}
.date{
  color: #808080;

}
.s_detail{
  margin-top: 0;
  font-size: 13px;
}
.s_result{
  margin-bottom: 30px;
}
.content{
  opacity: 0.8;
}
h3{
  font-family: arial, sans-serif;
  font-size: 18px;
  font-weight: normal;
}
.r_searches{
  display: flex;
  justify-content: right;
}
.r_searches ul{
  margin: 0;
}
.r_searches li{
  margin: 0 10px 10px 0;
}
.r_item{
  color: #1a0dab;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
}
#l_right{
  margin-left: 30px;
}
.page_number{
  margin-top: 15px;
  margin-left: 147px;
}

.pagination {
    display: inline-block;
    vertical-align: top;
    width:100%;
    height:60px;
    padding-bottom: 10px;
}

.pagination .page-item {
    display: inline-block;
    text-align: center;
    font-size: small;
    padding: 0;
    width: 35px;
    text-decoration: none;
    color: black;
}

.pagination .prev-item {
    display: inline-block;
    text-align: center;
    font-size: small;
    padding: 0;
    width: 50px;
    text-decoration: none;
    color: black;
}

.pagination .next-item {
    display: inline-block;
    text-align: center;
    font-size: small;
    padding: 0;
    width: 50px;
    text-decoration: none;
    color: black;
}
.pagination a {
  text-decoration: none;
  color: #429BF9;
}
.pagination a:hover {
  text-decoration: underline;
}
.pagination .active-item a {
  color: #000000;
}
.text_highlight {
  background: #FFFFFF;
  color: #000000;
  font-weight: bold;
}

@media screen and (max-width: 1024px) {
  .page_number{
    margin-top: 15px;
    margin-left: 50px;
  }
}
@media screen and (max-width: 768px) {
  .search input[type="text"]{
    box-sizing:border-box;
    height:46px;
    width: 350px;
    font-family: sans-serif;
    font-size: 16px;
    padding: 0 20px;
    border: 0.5px solid #d9d9d9;
    box-shadow: 0 4px 2px -2px #e6e6e6;
  }
  .page_number{
    margin-top: 15px;
    margin-left: 10px;
  }
}
</style>



