// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import { Base64 } from 'js-base64'
import axios from 'axios'
import Paginate from 'vuejs-paginate'
import TextHighlight from 'vue-text-highlight';
import router from './router'
import App from './App'

// reference axios globally
window.axios = axios
window.Base64 = Base64

if (!localStorage.getItem('bitdbNode')) {
  localStorage.setItem('bitdbNode', 'https://bitdb.network/q/')
}
if (!localStorage.getItem('bitdbApiKey')) {
  localStorage.setItem('bitdbApiKey', 'qpl84tsdqd6yxd3hpxhj5ngr3rp0pvlweqy7p7rzfy')
}
if (!localStorage.getItem('blockExplorerRpc')) {
  localStorage.setItem('blockExplorerRpc', 'https://cashexplorer.bitcoin.com')
}
if (!localStorage.getItem('resultsPerPage')) {
  localStorage.setItem('resultsPerPage', 20)
}

Vue.config.productionTip = false

Vue.component('paginate', Paginate)
Vue.component('text-highlight', TextHighlight);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

Vue.filter('striphtml', function (value) {
  let div = document.createElement('div')
  div.innerHTML = value
  let text = div.textContent || div.innerText || ''
  return text
})
Vue.filter('toTitle', function (text) {
  let resultString = text.split(' ')[0]
  if (text.includes(' ')) {
    resultString = resultString + ' ' + text.substr(text.indexOf(' ') + 1).split(/[.|:|;|-|!|?|<|&]+/).slice(0, 1).join(' ')
  }
  return resultString
})
Vue.filter('highlight', function(words, query) {
    return words.replace(query, '<span class="highlight">' + query + '</span>')
});



